:root {

	/* Z-index levels */
}

.rootFixed {
	-ms-flex-line-pack: stretch;
	    align-content: stretch;
	-webkit-box-align: stretch;
	    -ms-flex-align: stretch;
	        align-items: stretch;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	overflow: hidden;
	position: relative;
	width: 100%;
}

.rowFixed {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}

.dividerFixed {
	background-clip: padding-box;
	cursor: pointer;
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
	position: relative;
	z-index: calc(100 - 1);
}

.dividerFixed::after {
	content: "";
	position: absolute;
}

.leadingFixed {
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
}

.columnFixed > .dividerFixed {
	width: 1px;
	cursor: ew-resize;
}

.columnFixed > .dividerFixed::after {
	height: 100%;
	left: -10px;
	top: 0;
	width: 21px;
}

.rowFixed > .dividerFixed {
	height: 1px;
	cursor: ns-resize;
}

.rowFixed > .dividerFixed::after {
	height: 21px;
	top: -10px;
	left: 0;
	width: 100%;
}

.trailingFixed {
	-webkit-box-flex: 1;
	    -ms-flex: 1 1 0%;
	        flex: 1 1 0%;
}

.trailingFixed, .leadingFixed {
	overflow: auto;
}

.rootFixed.collapsedFixed {
	display: block;
	overflow: auto;
}
